import * as React from 'react';
import { graphql } from 'gatsby';
import Layout from '~src/components/Layout/Layout';
import { MarkdownContent } from '~src/components/Sections/MarkdownContent/MarkdownContent';
import styles from './post.module.scss';
import { Page as PageData } from './__generated__/Page';

// The normal <a> tag is modified here (so that internal links use gatsby-link/LocalizedLink
// More info:
// https://www.gatsbyjs.org/docs/mdx/customizing-components/

const PageTemplate: React.FC<{ data: PageData }> = ({ data }) => {
	return (
		<>
			<Layout template="page" data={data}>
				<div className="inner">
					<article className={''}>
						<header className={styles.postFullHeader}>
							<h1 className={styles.postFullTitle}>{data.page.frontmatter.title}</h1>
						</header>
						<MarkdownContent body={data.page.body} />
					</article>
				</div>
			</Layout>
		</>
	);
};

export default PageTemplate;

export const PageTemplateQuery = graphql`
	query Page($title: String!, $locale: String!) {
		page: mdx(frontmatter: { title: { eq: $title } }, fields: { locale: { eq: $locale } }) {
			frontmatter {
				title
			}
			excerpt
			body
		}
	}
`;
